<template>
  <x-crud :loading="loading">
    <x-form
    v-model="formData"
    :values="formData"
    @submit="onsubmit">
      <a-row :gutter="[24, 24]">
        <a-col
          md="12"
          :lg="{ span: 12, offset: 0 }"
          :xl="{ span: 12, offset: 0 }"
          class="col-form"
        >
          <x-input
            label="Название"
            name="name"
            type="text"
            validation="required"
            :disabled="disabled"
          ></x-input>
          <x-input
            label="Номинация"
            name="nomination"
            :options="$store.getters['app/optionNomination']"
            type="select"
            validation="required"
            :disabled="disabled"
          ></x-input>
          <x-input
            label="Элементы заполнения"
            type="checkbox"
            name="checkboxes"
            :options="checkboxes"
            error-behavior="submit"
            :disabled="disabled"
          >
          </x-input>
        </a-col>
        <a-col
          md="12"
          :lg="{ span: 12, offset: 0 }"
          :xl="{ span: 12, offset: 0 }"
          class="col-form"
        >
        <x-input
            type="number"
            name="max_count_achievement"
            label="Максимальное количество достижений"
            validation="required|number|min:0|max:50"
            min="0"
            max="200"
            :disabled="disabled"
          >
        </x-input>
          <x-input
            type="number"
            name="count_score_per_achievement"
            label="Балл за достижение"
            validation="required|number|min:0|max:50"
            min="0"
            max="200"
            :disabled="disabled"
          >
          </x-input>
          <x-input
            label="Период"
            name="period"
            :options="$store.getters['app/optionPeriod']"
            type="select"
            validation="required"
            :disabled="disabled"
          ></x-input>
        </a-col>
        <a-col span="24" v-if="formData.checkboxes && formData.checkboxes.includes('hint')">
          <x-input
            label="Подсказка"
            name="hint_text"
            type="textarea"
            :disabled="disabled"
            :validation="`required|max:${textareaMaxLength},length`"
            :help="formData.test && `Максимально ${textareaMaxLength}. ${(textareaMaxLength - formData.test.length <= 0) ? 0 : textareaMaxLength - formData.test.length} символось осталось.`"
          ></x-input>
        </a-col>
        <a-col
        :md="{span: 6, offset: 9}"
        :lg="{span: 6, offset: 9}"
        :xl="{span: 6, offset: 9}">
          <div class="crud-form-button">
            <a-button type="primary" block html-type="submit">
              {{ getButtonAction }}
            </a-button>
          </div>
        </a-col>
      </a-row>
    </x-form>
  </x-crud>
</template>

<script>
import crudMixin from "@/mixins/crudMixin";

export default {
  props: ["action", "id"],
  mixins: [crudMixin],
  data() {
    return {
      formData: {
        checkboxes: []
      },
      indicator: null,
      entityName: "indicator",
      textareaMaxLength: 512,
      checkboxes: [
        {value: 'degree_participation', label: 'Степень участия'},
        {value: 'document', label: 'Документ'},
        {value: 'other', label: 'Другое'},
        {value: 'url', label: 'Ссылка на видео'},
        {value: 'hint', label: 'Подсказка'},
      ]
    }
  },
  methods: {
    async onsubmit() {
      if (this.action === 'read') {
        this.$router.push(`/indicator/edit/${this.id}`)
        return
      }
      this.checkboxes.map(checkbox => {
        this.formData[checkbox.value] = this.formData.checkboxes.includes(checkbox.value)
      })
      if (!this.formData.checkboxes.includes('hint')) this.formData.hint_text = null
      await this.saveEntity(this.formData)
    },
    async getEntities(entity) {
      try {
        await this.$store.dispatch('app/getEntities', {
          entityName: entity,
        })
      } catch (e) {
        console.log(e)
      }
    },
    renderCheckboxes() {
      this.formData.nomination = `${this.formData.nomination.id}`
      this.formData.period = `${this.formData.period.id}`
      if (this.formData.hint_text) this.formData.checkboxes.push('hint')

      for (let key in this.formData) {
        if (this.checkboxes.some(checkbox => checkbox.value === key && this.formData[key])) this.formData.checkboxes.push(key)
      }
    }
  },
  async created() {
    await this.getEntities('nomination')
    await this.getEntities('period')
    if (this.action !== 'create') {
      await this.getEntity(this.id)
      this.renderCheckboxes()
    }
    this.loading = false
    console.log(this.formData)
  },
};
</script>

